.map {
  width: 100vw;
  height: 70vh;
  background-color: #ccc;
  position: sticky;
  top: 0;
  left: 0;
}
.map > div {
  width: 100%;
  height: 100vh;
  margin: 'auto';
}

@media only screen and (min-width: 1100px) {
  .map {
    width: 70vw;
    height: 100vh;
    background-color: #fff;
  }
}
